body, html {
  background-image: url(/tripler-react-app/static/media/HomePage.b30d29d1.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height:100%;
}

#container {
  /*display: inline-block;*/
  height: 100%;
}

#homePageContainer {
  height: 100%;
}

#homeBannerContainer {
  max-height: 150px;
  overflow-y: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  color: white;
}

#logo {
  width: 300px;
  float:left;
}

#logoImg {
  margin-top: 20px;
  float: left;
  height: 60px;
}

#title {
  font-size: 40px;
}

#tagline {
  margin-left: 20px;
  margin-top: 10px;
}

#homeSearchContainer {
  height:200px;
  width: 600px;
  background-color: rgba(255, 255, 255, 0.8);
  margin: 0 auto;
  margin-top: 100px;
}

#searchPrompt {
  font-size: 25px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  text-align: center;
  padding-top: 30px;
}

#homeSearchBarContainer {

}

#homeSearchBar {
  width: 400px;
  margin: 10px auto;
  padding: 10px;
  text-align: left;
  margin-left: 60px;
}

#homeSearchButton {
  margin: 10px auto;
  padding: 10px;
}

#searchContainer {
  width: 491px;
  float: right;
  margin: 40px;
}

#searchBar {
  width: 400px;
  padding: 10px;
  text-align: left;
  margin-right: 0px;
}

#searchButton {
  float: right;
  padding: 10px;
}

#resultsBannerContainer {
  display: inline-block;
  width: 100%;
  max-height: 100px;
  overflow-y: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  color: white;
}

#resultsMain {
  height:100%;
  width: 100%;
  background-color: rgba(255, 255, 255);
  margin: 0 auto;
  margin-top: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-column-count: 2;
          column-count: 2;
}

#filter {
  width: 10%;
  column: 1;
  -webkit-column-span: 1;
          column-span: 1;
  -webkit-column-count: 1;
          column-count: 1;
  margin-left: 20px;
}

#results {
  column: 2;
  -webkit-column-count: 1;
          column-count: 1;
  -webkit-column-span: 1;
          column-span: 1;
  width: 100%;
  float: left;
}

#resultCard {
  list-style-type: none;
  width: 95%;
  margin: 0 auto;
  padding: 0;
  float: left;
}

#listItem {
  background-color: #f5f5f5;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 25px;
  padding-top: 1px;
  margin-bottom: 10px;
  position: relative;
}

#listItem:hover {
   background-color: #f5f5f5;
 }

a {
  position: absolute;
  left: 200px;
  color: #777;
}

#resultTitle {
  font-size: 24pt;
  margin-top: 15px;
  margin-bottom: -15px;
}

#resultCity {
  font-size: 16pt;
}

#eventlist {
  margin-top: 20px;
  display: inline;
  list-style: none;
  margin-left: -40px;
}

#eventlist li {
  display: inline;
}

#eventlist li:after {
  content: ", ";
}

#eventlist li:last-child:after {
  content: "";
}

ul {
  list-style-type: none
}
